// BlockQuote Mixins //

@mixin blockquote($color) {
  border-color: $color;
  .blockquote-footer {
    color: $color;
    background: transparent;
    padding: 0;
  }
}
@mixin alert-variant($color) {
  color: $color;
}
@mixin alert-variant-fill($color) {
  color: lighten($color, 80%);
  background: $color;
}