/* Js-grid */

.jsgrid {
	.jsgrid-button {
		background-image: url('../../../assets/images/sprites/jsgrid-icons.png');
	}
	.jsgrid-grid-header {
		border: 1px solid $border-color;
		border-top: 0;
	}
	.jsgrid-table {
		@extend .table;
		th {
			font-weight: initial;
			font-family: $type-1;
			font-weight: $font-weight-bold;
		}
		.jsgrid-filter-row {
			input[type=text],
			select {
				@extend .form-control;
				padding: 0.75rem 0.75rem;
				border: 0;
				outline: 1px solid #dee2e6;
				color: #c9c8c8;
				appearance: none;
				background: url('../../../assets/images/arrow-down.svg');
				background-repeat: no-repeat;
				background-position: 99% center
			}
			input[type=number] {
				@extend .form-control;
				padding: .56rem 0;
			}
		}
	}
	.jsgrid-header-sort {
		&:before {
			margin-top: 10px;
			float: right;
		}
	}
	.jsgrid-header-sort-asc {
		&:before {
			border-color: transparent transparent $body-color;
		}
	}
	.jsgrid-header-sort-desc {
		&:before {
			border-color: $body-color transparent transparent
		}
	}
	.jsgrid-pager {
		line-height: 2;
		@extend .pagination;
		.jsgrid-pager-nav-button,
		.jsgrid-pager-page {
			@extend .page-item;
			a {
				@extend .page-link;
			}
		}
	}
	.jsgrid-pager-current-page {
		padding: 0.5rem 0.75rem;
		font-weight: initial;
		line-height: 1.25;
	}
	.jsgrid-header-row {
	border-top: 1px solid $border-color;
	}
}